import Api from '.././baseApi'

export default class showApi {
    static index(data, callback, errorBack) {
        Api.request(Api.url() + 'user3/' + 'index', callback, errorBack, data)
    }

    static add(data, callback, errorBack) {
        Api.request(this.uri() + 'add', callback, errorBack, data)
    }

    static delete(data, callback, errorBack) {
        Api.request(this.uri() + 'delete', callback, errorBack, data)
    }

    static group_spinner(data,callback, errorBack) {
        Api.request(this.uri() + 'group_spinner', callback, errorBack,data)
    }

    static company_spinner(data,callback, errorBack) {
        Api.request(this.uri() + 'company_spinner', callback, errorBack,data)
    }

    static uri() {
        return Api.url() + 'user/'
    }

}
