<style scoped lang="less">
    .pages {
        text-align: left;
        padding: 0 20px;
        .form {
            width: 40%;
            margin-top: 30px;
        }
    }

</style>


<template>
    <div class="pages">
        <H3>基础信息</H3>
        <div class="line"></div>
        <el-form class="form" ref="form" :model="form" label-width="120px">
            <el-row>
                <el-form-item label="选择企业" style="width:500px">
                <el-select class="header-col-select" v-model="user_id" placeholder="请选择">
                    <el-option v-for="item in form.company"
                            :value="item.id"
                            :label="item.company"
                            :key="item.id">
                    </el-option>
                </el-select>
               </el-form-item>

               <el-form-item label="选择分组" style="width:500px">
                <el-select class="header-col-select" v-model="group_id" placeholder="请选择">
                    <el-option v-for="item in list.group"
                            :value="item.id"
                            :label="item.name"
                            :key="item.id">
                    </el-option>
                </el-select>
               </el-form-item>
                
            </el-row>
            <el-form-item>
                <el-button type="primary" :loading="isLoading" @click="onSubmit">提交</el-button>
                <el-button @click="back">返回</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import api from '../../js/api/member/show'
    import baseApi from '../../js/api/baseApi'
    export default {
        data() {
            return {
                form: {},
                list:{},
                isLoading: false,
                user_id:null,
                group_id:null,
                
            }
        },
        mounted() {
            this.init();

        },
        methods: {
            init() {
                    this.loadData()
            },
            onSubmit() {
                this.isLoading = true;
                if (!baseApi.isStrValid(this.user_id)) {
                    this.$message.error('请选择企业');
                    return;
                }

                if (!baseApi.isStrValid(this.group_id)) {
                    this.$message.error('请选择分组');
                    return;
                }
                api.add({user_id:this.user_id,group_id:this.group_id}, () => {
                    this.isLoading = false;
                    this.$message.success('保存成功!');
                    this.$router.push({name: 'show'});
                    this.$route.meta.keepAlive = false;
                }, () => {
                    this.isLoading = false;
                })
            },

            loadData() {
                api.company_spinner({id: this.$route.query.id},res => {
                    this.form = res.data;
                })

                api.group_spinner({id: this.$route.query.id},res => {
                    this.list = res.data;
                })
            },
            back() {
                this.$router.back();
            },
            edit() {
                this.isNoEdit = false;
            }
        }
    }
</script>
